
button {
  padding: 12px 16px; /* Increase padding for larger buttons */
  font-size: 18px;
  background-color: #e0e0e0;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #d4d4d4;
}

.app-container {
  text-align: center;
}

.season-header {
  border: 18 px solid black;
  /* margin: auto; */
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.popup-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  position: relative;
}

.close-btn {
  position: absolute;
  top: 5px;
  right: 10px;
  cursor: pointer;
}

.years-row, .seasons-row {
  display: flex;
  justify-content: space-around;
  margin: 10px 0;
}

.years-row button, .seasons-row button {
  padding: 10px 20px;
  cursor: pointer;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
